body {
    background-color: #20272e;
}

.ma-container {
    font-size: 15px;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #5b6670;    
}

.site-description {
    margin-top: 20px;
    margin-left: 5px;
}

.settings-panel-container {
    display: flex;
    margin: auto;
    flex-direction: column;
    border: 1px solid #5b6670;
    padding: 30px;
    width: 25%;
    box-sizing: border-box;
}

.settings-name {
    color:#5b6670;
    font-size: 13px;
    margin-right: 155px;
    margin-top: 10px;
    box-sizing: border-box;
}

.settings-input {
    padding: 8px;
    color: #5b6670;
    background-color: #20272e;
    border-color: #5b6670;
    border-radius: 5px;
    box-sizing: border-box;
}

.buttons-container {
    display: flex;
    margin: auto;
    justify-content: center;
    padding: 20px;
}

.button {
    background-color: #5b6670;
    color: #20272e;
    padding: 2.5px;
    margin: 5px;
    width: 5%;
}

#consoleDisplay {
    background-color: #20272e;
    color: #5b6670;
    font-size: 15px;
    text-align: center;
    height: 50px;
    overflow-y: scroll;
    margin-left: auto;
    margin-right: auto;
    border: 1px solid #5b6670;
    padding: 100px;
    width: 25%;
    box-sizing: border-box;
}